import defaultAvatar from '../../images/blank-avatar.png';
import {mapState} from 'vuex';
import WeVue from 'we-vue';
export default {
  data() {
   return {
     isLogin: false,
     defaultAvatar: defaultAvatar
   };
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      if (!vm.$store.state.userInfo.Profile && localStorage.getItem('userInfo') === null) {
            vm.$router.push('./login');
      }
      vm.$store.commit('isMenu', true);
    });
  },
  created() {
    this.$nextTick(() => {
      this.$store.commit('isLoading', false);
    });
  },
  computed: mapState({
    myInfo: (data) => {
      if (!data.userInfo.Profile && localStorage.getItem('userInfo')) {
          data.userInfo = JSON.parse(localStorage.getItem('userInfo'));
      }
      return data.userInfo;
    }
  }),
  methods: {
    tipToast (msg) {
      WeVue.Toast({
        duration: 2000,
        message: msg,
        type: 'text'
      });
    },
    getUserProfile(userId) {
      WeVue.Indicator.open({
        text: 'loading',
        spinnerType: 'double-snake'
      });
      this.axios({
        method: 'get',
        url: this.$store.state.passportUrl + '/api/Membership/GetUserProfile?userId=' + userId
      }).then((res) => {
        res = res.data;
        if (!res.IsError) {
          WeVue.Indicator.close();
          this.$store.commit('CurrentUser', res.Data);
          this.getUserAccounting(userId);
        } else {
          WeVue.Indicator.close();
          this.tipToast(res.Message);
        }
      }).catch(() => {
        WeVue.Indicator.close();
        this.tipToast('网络超时，请稍后再试！');
      });
    },
    getUserAccounting(userId) {
      this.axios({
        method: 'get',
        url: this.$store.state.passportUrl + '/api/Membership/GetUserAccounting?userId=' + userId
      }).then((res) => {
        if (!res.data.IsError) {
          res = res.data.Data;
          this.$store.state.isLogin = false;
          this.$store.commit('CurrentUser', {Mobile: res.Mobile, type: 'Mobile'});
          WeVue.Indicator.close();
        } else {
          WeVue.Indicator.close();
          this.tipToast(res.data.Message);
          this.$router.push('./login');
        }
      }).catch(() => {
        WeVue.Indicator.close();
        this.tipToast('网络超时，请稍后再试！');
      });
    }
  },
  mounted() {
  },
  activated() {
    this.$nextTick(() => {
      if (this.myInfo.UserId) {
        this.getUserProfile(this.myInfo.UserId);
      }
      this.$store.commit('isLoading', false);
    });
  }
};



